@import url(https://fonts.googleapis.com/css2?family=Big+Shoulders+Display:wght@700&family=Krona+One&family=Quantico:wght@700&family=Russo+One&family=Teko:wght@500&family=Ubuntu&display=swap);
:root {
  --main: #8D85F1;
  --sec: #5cdeff;
  --gray: #141414;
}

* {
  margin: 0;
  box-sizing: border-box;
  text-decoration: none;
}

.master-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: #141414;
  background-color: var(--gray);
  background: url(/static/media/background.37c45d1a.jpg) no-repeat center fixed;
  background-size: cover;
  color: white;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translate(0, -50px);
            transform: translate(0, -50px);
  }
  to {
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    -webkit-transform: translate(0, -50px);
            transform: translate(0, -50px);
  }
  to {
    opacity: 1;
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
}

.fade-in {
  -webkit-animation-name: fadeIn;
          animation-name: fadeIn;
  transition: opacity 2s;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
}
header {
  position: fixed;
  top: 0;
  display: flex;
  justify-content: space-between;
  height: 100px;
  width: 100%;
  padding: 0 5%;
}

header .logo {
  display: flex;
  justify-content: center;
  align-items: center;  
}

/* header .logo img {
  padding: 0 30px;
  height: 100px;
  width: auto;
} */

header .logo p {
  font-family: 'Krona One', sans-serif;
  font-size: 40px;
}

header .nav {
  display: flex;
  align-items: center;
  /* border: solid red 4px; */
}

header .nav button {
  border: none;
  background: none;
  font-family: 'Teko', sans-serif;
  font-size: 30px;
  text-decoration: underline;
  text-underline-offset: 5px;
  text-decoration-thickness: 7px;
  -webkit-text-decoration-color: transparent;
          text-decoration-color: transparent;
  cursor: pointer;
  color: white;
  padding: 0 1vw;
}

header .nav .selected {
  -webkit-text-decoration-color: var(--main);
          text-decoration-color: var(--main);
  transition: -webkit-text-decoration-color 0.5s;
  transition: text-decoration-color 0.5s;
  transition: text-decoration-color 0.5s, -webkit-text-decoration-color 0.5s;
}
.welcome-div {
  display: flex;
  flex-direction: column;
  align-items: center;  
}

.welcome-div img {
  width: 400px;
}

.welcome-div h1, .welcome-div h2 {  
  font-family: 'Quantico', sans-serif;
  font-size: 50px;
  color: var(--main)
}

.welcome-div h2 {  
  font-size: 40px;
  color: var(--sec)
}
.about-me-div {
  display: flex;
  /* width: 600px; */
  /* border: solid green 4px; */
}

.about-me-div div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* border: solid red 4px; */
  width: 400px;
  margin-right: 100px;
}

.about-me-div p {
  font-family: 'Ubuntu', sans-serif;
  text-align: center;
  font-size: 25px;
  margin: 30px 0;
}

.about-me-div img {
  width: 400px;
  height: 400px;
}
.project-card-div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: #222;
  border: solid #333 8px;
  border-radius: 20px;
  height: 30vh;
  width: 30vh;
  cursor: pointer;
  margin: 30px;
}

.project-card-on-hover {
  border: solid 8px var(--sec);
}

.project-card-div img {
  height: 17vh;
  width: 17vh;
  margin-top: 3vh;
}

.project-card-div p {
  font-family: 'Quantico', sans-serif;
  text-align: center;
  color: var(--main);
  font-size: 3.5vh;
  margin-bottom: 1vh;
}

.project-card-on-hover p {
  color: var(--sec);
}
.projects-div {
  display: grid;
  grid-template-columns: auto auto;
}


